@use 'base/variables' as v;
@use 'base/mixins' as m;

.formulario-contenido {
    text-align: left;

    @include m.tablet {
        @include m.grid(2, 2rem);
        align-items: center;
    }

    picture {
        display: none;

        @include m.tablet {
            display: block;
        }
    }
}

.formulario {
    .bloque {
        font-size: 2rem;
        margin-bottom: 2rem;

        &:last-of-type {
            margin-bottom: 4rem;
        }
    }

    label {
        font-weight: 700;
    }

    input,
    select {
        display: block;
        border: 3px solid v.$primario;
        padding: 1rem 1rem 1rem 2rem;
        width: 100%;
    }

    input[type="submit"] {
        padding: 1rem;
        background-color: v.$primario;
        color: v.$blanco;
        font-weight: 700;
        border: none;
        transition: background-color 300ms ease-in;
        
        &:hover {
            cursor: pointer;
            background-color: darken(v.$primario, 5);
        }
    }
}