@use 'base/variables' as v;
@use 'base/mixins' as m;

.proceso {
    text-align: left;
    margin-bottom: 2rem;

    &:last-of-type {
        margin-bottom: 0;
    }
    
    @include m.tablet {
        display: flex;
        gap: 2rem;
        align-items: center;
    }

    picture,
    div {
        flex: 1;
    }

    &:nth-child(even) {
        picture {
            order: 2;
        }
    }
    
    img {
        width: 100%;
        height: 20rem;
        object-fit: cover;
        overflow: hidden;
        
        margin-bottom: 2rem;
        
        @include m.tablet {
            margin-bottom: 0;
            height: 25rem;
        }

        @include m.desktop {
            height: 30rem;
        }
    }

    h3 {
        margin: 0;
        text-align: left;
        color: v.$primario;

        &::after {
            content: '';
            display: block;
            border-bottom: 3px solid v.$primario;
            width: 10rem;
        }
    }
}