@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    @include m.grid(2, 2rem);

    @include m.desktop {
        @include m.grid(4, 2rem);
    }

    .imagen {
        img {
            height: 30rem;
            width: 30rem;
            object-fit: cover;
            object-position: center;
        }

        @include m.desktop {
            &:nth-child(1) {
                grid-row: 1 / 3;
                
                img {
                    height: 62rem; /* 30 * 2 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(2) {
                grid-column: 2 / 4;
                
                img {
                    width: 62rem; /* 30 * 2 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(4) {
                grid-row: 2 / 4;
                
                img {
                    height: 62rem; /* 30 * 2 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(5),
            &:nth-child(6) {
                grid-column: 3 / 5;
                
                img {
                    width: 62rem; /* 30 * 2 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(7) {
                grid-row: 3 / 5;
                
                img {
                    height: 62rem; /* 30 * 2 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(8) {
                grid-column: 2 / 5;
                
                img {
                    width: 94rem; /* 30 * 3 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(9) {
                grid-column: 1 / 3;
                
                img {
                    width: 62rem; /* 30 * 2 + 2 */
                }
            }
        }

        @include m.desktop {
            &:nth-child(10) {
                grid-column: 3 / 5;
                
                img {
                    width: 62rem; /* 30 * 2 + 2 */
                }
            }
        }
    }
}