@use 'base/variables' as v;
@use 'base/mixins' as m;

.nav-principal {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    margin: 2rem 0;
    
    @include m.tablet {
        margin: 0;
        flex-direction: row;
    }    

    a {
        text-align: center;
        color: v.$blanco;
        text-transform: uppercase;
        font-weight: 700;
        font-size: 2rem;
        display: inline-block;

        &:hover {
            color: v.$primario;
        }
    }

    .activo {
        color: v.$primario;
    }
}