@use 'base/variables' as v;
@use 'base/mixins' as m;

.nosotros-contenedor {
    text-align: left;
    @include m.tablet {
        @include m.grid(2, 2rem);
        align-items: center;
    }

    img {
        transform: rotate(-5deg);
        width: 50rem;
        padding: 2rem 2rem 6rem 2rem;
        border: 1px solid v.$grisClaro;
        box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;

        margin-bottom: 5rem;
        
        @include m.tablet {
            margin-bottom: 0;
        }
    }

    cite {
        border-left: 3px solid v.$primario;
        padding: 1rem;
        font-weight: 700;
        color: v.$primario;
        background-color: lighten(v.$primario, 70);
    }

    margin-bottom: 5rem;
}