@use 'base/variables' as v;
@use 'base/mixins' as m;

.contenido-principal {
    background-color: v.$blanco;
    padding: 4rem 2rem 2rem 2rem;
    text-align: center;

    @include m.tablet {
        padding: 4rem;
        margin-top: -10rem;
        position: relative;
    }

    .iconos {
        margin-top: 4rem;

        .listado-iconos {
            @include m.grid(1, 1rem);
            
            @include m.tablet {
                @include m.grid(3, 1rem);
            }
            
            li {
                margin: 0 auto;
            }

            img {
                width: 15rem;
            }

            p {
                font-family: v.$fuenteCursiva;
                font-size: 3.2rem;
                color: v.$primario;
            }
        }
    }
}