@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {
    background-color: v.$primario;
    text-align: center;

    .footer-contenido {
        @include m.tablet {
            @include m.grid(3, 3rem);
            justify-content: space-between;
        }

        h3 {
            color: v.$secundario;
            font-family: v.$fuenteCursiva;
            margin: 0;
            font-weight: 400;
        }

        p {
            margin: 0;
        }

        .box {
            margin-bottom: 3rem;

            @include m.tablet {
                margin-bottom: 0;
            }
        }

        padding: 2rem 1rem;

    }

    .copyright {
        margin: 0;
        color: v.$blanco;
        padding: 2rem;
    }
}
